a { color: inherit; text-decoration: none; }
.wrap {
    .about { text-align: center; }
    .about p {
        display: inline-block;
        border: 2px solid #fff;
        margin-top: 5%;
        padding: 10px;
    }
    .division {
        margin: 5% 0;
        background: #000;
        color: #fff;
        h2 { padding: 25px 10px; border: 2px solid #000; color: #000; background: #fff; }
    
        .div1 p:nth-of-type(1) { text-align: center; padding-top: 20px; }
        .div1 p:nth-of-type(1) img { width: 50%; }
        .div1 p:nth-of-type(2) { font-size: 18px; text-align: center; font-weight: bold; padding: 20px 0; }

        .div2 p:nth-of-type(1) { text-align: center; }
