.loadWrap {
    font-size: 50px;
    min-height: 300px;
    text-align: center;
    line-height: 300px;
    .loadicon { 
        font-size: 100px;
        animation: load infinite 5s linear;
    }
    @keyframes load {
        from { transform: rotate(0deg); }
        to { transform: rotate(360deg); }
        
    }
}